.Button {
  text-align: center;
  background-color: #000;
  color: #fff;
  width: 170px;
  height: 54px;
  font-size: 14px;
  border: 0;
  border-radius: 3px;
  cursor: pointer;
}

.Button:hover {
  opacity: 0.7;
}

.Button.disabled {
  background-color: grey;
}

.Button.disabled:hover {
  opacity: 1;
  cursor: default;
}
